import { RendererCanvas, distanceTo } from '../../../lib'

import { PointPicking } from './BinSelection';

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id)

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '1000px';
        div.style.height = '600px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation
        this.renderer = new Renderer(div);
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {
    tradition: boolean = false;
    debugger: boolean = false
    text: HTMLDivElement;
    pts: number[][] = [];
    selPt: number[] | undefined = [];
    log: boolean = false;
    picking: PointPicking;
    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation

        const divMenu = document.createElement('div');
        div.appendChild(divMenu);

        const btnInput = document.createElement('div');
        btnInput.style.width = '100px';
        btnInput.style.height = '50px';
        btnInput.textContent = '1000';
        btnInput.contentEditable = 'true';
        btnInput.style.background = '#fff';
        btnInput.style.height = '28px';
        btnInput.onkeydown = () => {
            this.init(+btnInput.textContent);
        }
        divMenu.appendChild(btnInput);

        const btn = document.createElement('button');
        btn.style.width = '100px';
        btn.style.height = '50px';
        btn.textContent = 'Search type';
        btn.onclick = () => {
            this.tradition = !this.tradition;
        }
        divMenu.appendChild(btn);

        const btnDebugger = document.createElement('button');
        btnDebugger.style.width = '80px';
        btnDebugger.style.height = '50px';
        btnDebugger.textContent = 'debugger';
        btnDebugger.onclick = () => {
            this.debugger = !this.debugger;
        }
        divMenu.appendChild(btnDebugger);

        const text = document.createElement('div');
        text.style.height = '50px';
        text.onclick = () => {
            this.tradition = !this.tradition;
        }
        div.appendChild(text);
        this.text = text;
        this.init();
    }
    public init(num = 1000000) {
        this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);

        this.pts = []
        for (let i = 0; i < num; ++i) {
            const p = [Math.random() * this.canvas.width, Math.random() * this.canvas.height];
            this.pts.push(p);
        }

        this.picking = new PointPicking(this.pts)

        this.render(this.ctx);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop

        for (let i = 0; i < this.pts.length; i++) {
            ctx.fillStyle = '#f00';
            ctx.beginPath();
            ctx.arc(this.pts[i][0], this.pts[i][1], 2, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        };

        if (this.log) {
            // this.pmap.CVisLineGrid(ctx);

            // if (this.pixel) {
            //     ctx.beginPath();
            //     ctx.moveTo(this.pixel.recVec[0].x, this.pixel.recVec[0].y);
            //     // console.log(this.pixel.recVec)
            //     for (let j = 1; j < this.pixel.recVec.length; ++j) {
            //         ctx.lineTo(this.pixel.recVec[j].x, this.pixel.recVec[j].y);
            //         // console.log(pixelMap.listPixel[i].nPix[j].vec.x, pixelMap.listPixel[i].nPix[j].vec.y);

            //     }
            //     ctx.closePath();
            //     ctx.strokeStyle = '#00f'; // ColorUtility.HexToCanvasColor(color, 1);
            //     ctx.stroke();
            // }
        }

        if (this.selPt) {
            this.ctx.fillStyle = '#0ff';
            this.ctx.beginPath();
            this.ctx.arc(this.selPt[0], this.selPt[1], 4, 0, Math.PI * 2);
            this.ctx.closePath();
            this.ctx.fill();
        }

        // const pList = this.picking.getGrid();
        // for(let i = 0 ; i < pList.length; ++i) {
        //     const c = pList[i];
        //     drawRect(this.ctx, c.recVec)
        // }


        this.isStatic = true;

    }
    public mouseMove(x: number, y: number): void {
        // console.log('=========================')
        // console.log(this.picking)

        if (this.tradition) {
            var start = performance.now();  // log start timestamp
            this.selPt = getClosestPt(this.pts, [x, y]);
            var end = performance.now();  // log end timestamp
            var diff0 = end - start;
            // console.log('diff 0', diff0)
            this.text.textContent = 'total num pts: ' + this.pts.length + ', time: ' + diff0.toFixed(8).toString() + 'ms';
        } else {
            let start2 = performance.now();

            if (this.selPt) {
                console.log(this.selPt)
            }

            this.selPt = this.picking.picking(x, y);
            let end2 = performance.now();
            var diff2 = end2 - start2;
            // console.log('diff 1', diff2)
            this.text.textContent = 'total num pts: ' + this.pts.length + ', time: ' + diff2.toFixed(8).toString() + 'ms';

            if (this.debugger) {
                const p = this.picking.getCell();
                // console.log(p)

                for (let i = 0; i < p.length; ++i) {

                    this.ctx.beginPath();
                    this.ctx.moveTo(p[i].recVec[0].x, p[i].recVec[0].y);
                    // console.log(p[i].recVec)
                    for (let j = 1; j < p[i].recVec.length; ++j) {
                        this.ctx.lineTo(p[i].recVec[j].x, p[i].recVec[j].y);
                        // console.log(pixelMap.listPixel[i].nPix[j].vec.x, pixelMap.listPixel[i].nPix[j].vec.y);

                    }
                    this.ctx.closePath();
                    this.ctx.strokeStyle = '#00f'; // ColorUtility.HexToCanvasColor(color, 1);
                    this.ctx.stroke();
                }
            }
        }

        // const pList = this.picking.getGrid();
        // for(let i = 0 ; i < pList.length; ++i) {
        //     const c = pList[i];
        //     drawRect(this.ctx, c.recVec)
        // }

        // this.selPt = p;
        // console.log(this.selPt)

        // // console.log(pixel);
        // if (pixel) {
        //     this.pixel = pixel;
        // } else {
        //     this.pixel = undefined;
        // }

        if (this.selPt) {
            this.ctx.fillStyle = '#0ff';
            this.ctx.beginPath();
            this.ctx.arc(this.selPt[0], this.selPt[1], 4, 0, Math.PI * 2);
            this.ctx.closePath();
            this.ctx.fill();
        }


    }

}

const getClosestPt = (vs: number[][], v: number[]) => {
    let theDis = Number.MAX_VALUE;
    let vec: number[] = [];
    for (let i = 0; i < vs.length; ++i) {
        const dis = distanceTo(vs[i][0], vs[i][1], 0, v[0], v[1], 0);
        if (dis < theDis) {
            theDis = dis;
            vec = vs[i]
        }
    }
    return vec;
};
