export class PointPicking {
    private selPix: Pixel[];
    private pMap: PixelMap;
    private vs: NVector3[] = [];
    private min: NVector3 = new NVector3(Number.MAX_VALUE, Number.MAX_VALUE);
    private max: NVector3 = new NVector3(Number.MIN_VALUE, Number.MIN_VALUE);
    constructor(pts: number[][] = [], resX: number = 20, resY: number = 20) {
        for (let i = 0; i < pts.length; ++i) {
            const vec = new NVector3(pts[i][0], pts[i][1]);
            vec.id = this.vs.length;
            this.vs.push(vec);
            if (this.min.x > pts[i][0]) { this.min.x = pts[i][0] }
            if (this.min.y > pts[i][1]) { this.min.y = pts[i][1] }
            if (this.max.x < pts[i][0]) { this.max.x = pts[i][0] }
            if (this.max.y < pts[i][1]) { this.max.y = pts[i][1] }
        }
        this.pMap = new PixelMap(this.min, this.max, resX, resY);
        this.pMap.init();

        for (let i = 0; i < this.vs.length; ++i) {
            this.pMap.getPixelAtPoint(this.vs[i].x, this.vs[i].y).dataList.push(this.vs[i]);
        }
    }
    public picking(x: number, y: number, expanded = false) {
        this.selPix = [this.pMap.getPixelAtPoint(x, y)];
        if (expanded) { this.selPix.push(...this.selPix[0].nPix); }
        let dis = Number.MAX_VALUE;
        let id = -1;

        for (let j = 0; j < this.selPix.length; ++j) {
            const pixel = this.selPix[j];
            for (let i = 0; i < pixel.dataList.length; ++i) {
                const theDis = distance(pixel.dataList[i], this.pMap.pointer);
                if (theDis < dis) {
                    dis = theDis;
                    id = pixel.dataList[i].id;
                }
            }
        }
        if (id > -1) {
            return [this.vs[id].x, this.vs[id].y];
        } else {
            return undefined;
        }
    }
    public getCell() {
        return this.selPix;
    }
    public getCellGrid() {
        return this.pMap.listPixel;
    }
}
class PixelMap {
    public listPixel: Pixel[] = [];
    public rx: number = 0;
    public ry: number = 0;
    public ddx: number = 0.0;
    public ddy: number = 0.0;
    public v0: NVector3;
    public v1: NVector3;
    public pointer: NVector3 = new NVector3(-1, -1, -1);
    constructor(v0: NVector3, v1: NVector3, rx: number = 10, ry: number = 10) {
        this.rx = rx; this.ry = ry;
        this.ddx = 0.0; this.ddy = 0.0;
        this.v0 = v0; this.v1 = v1;
    }
    public init() {
        this.ddx = (this.v1.x - this.v0.x) / (this.rx - 1.0);
        this.ddy = (this.v1.y - this.v0.y) / (this.ry - 1.0);
        this.initGrid();
    }
    private initGrid() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let x = this.v0.x + i * this.ddx;
                let y = this.v0.y + j * this.ddy;
                let px = new Pixel(this);
                px.i = i; px.j = j;
                px.vec = new NVector3(x, y, 0);
                px.recVec.push(new NVector3(x - this.ddx * 0.5, y - this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x + this.ddx * 0.5, y - this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x + this.ddx * 0.5, y + this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x - this.ddx * 0.5, y + this.ddy * 0.5, 0.0));
                this.listPixel.push(px);
            }
        }
        this.initGridNeighbor();
    }
    private initGridNeighbor() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = j * this.rx + i;
                let px0 = this.listPixel[k];
                if (i > 0) { px0.nPix.push(this.listPixel[k - 1]); }
                if (i < this.rx - 1) { px0.nPix.push(this.listPixel[k + 1]); }
                if (j > 0) { px0.nPix.push(this.listPixel[k - this.rx]); }
                if (j < this.ry - 1) { px0.nPix.push(this.listPixel[k + this.rx]); }
                if ((i > 0) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx - 1]); }
                if ((i > 0) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx - 1]); }
                if ((i < this.rx - 1) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx + 1]); }
                if ((i < this.rx - 1) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx + 1]); }
            }
        }
    }
    public getPixelAtPoint(x: number, y: number, z: number = 0) {
        this.pointer.x = x - this.v0.x;
        this.pointer.y = y - this.v0.y;
        this.pointer.z = z - this.v0.z;
        let i = Math.round(this.pointer.x / this.ddx);
        let j = Math.round(this.pointer.y / this.ddy);
        if (i >= this.rx) { i = this.rx - 1; }
        if (j >= this.ry) { j = this.ry - 1; }
        if (i < 0) { i = 0; }
        if (j < 0) { j = 0; }
        return this.listPixel[j * this.rx + i];
    }
}
class Pixel {
    public map: PixelMap;
    public vec: NVector3;
    public nPix: Pixel[] = [];
    public recVec: NVector3[] = [];
    public i: number = -1;
    public j: number = -1;
    public k: number = -1;
    public dataList = []
    constructor(map: PixelMap, v: NVector3 = new NVector3(0, 0, 0)) {
        this.map = map;
        this.vec = v;
    }
};
class NVector3 {
    public id: number = -1;
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    constructor(x: number, y: number, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}
export const distance = (v0: NVector3, v1: NVector3): number => {
    return Math.sqrt((v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z));
};
export const distance2 = (v1: NVector3, v2: NVector3): number => {
    return (v1.x - v2.x) * (v1.x - v2.x) + (v1.y - v2.y) * (v1.y - v2.y) + (v1.z - v2.z) * (v1.z - v2.z);
};